.card_item {
    border-radius: 1.7rem;
    display: flex;
    justify-content: center;
    text-decoration: none;
    color: #000000;
}

.card_content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: .9;
    gap: .3rem;
}